@include font('Roboto-Medium-webfont', roboto-medium);
@include font('Roboto-Regular-webfont', roboto-regular);
@include font('Roboto-Black-webfont', roboto-black);
@include font('Roboto-Light-webfont', roboto-light);
@include font('Roboto-Bold-webfont', roboto-bold);
@include font('Roboto-Italic-webfont', roboto-italic);

@include font('Helvetica', helvetica);
@include font('Helvetica-Oblique', helvetica-oblique);
@include font('Helvetica Bold', helvetica-bold);

@include font('Poppins-SemiBold', poppins-semibold);

@include font('Lora-Italic', lora-italic);

@include font('GreatVibes-Regular', greatVibes-regular);

@mixin roboto-medium {
	font-family: roboto-medium, Arial, sans-serif; }
@mixin roboto-regular {
	font-family: roboto-regular, Arial, sans-serif; }
@mixin roboto-black {
	font-family: roboto-black, Arial, sans-serif; }
@mixin roboto-light {
	font-family: roboto-light, Arial, sans-serif; }
@mixin roboto-bold {
	font-family: roboto-bold, Arial, sans-serif; }
@mixin roboto-italic {
	font-family: roboto-italic, Arial, sans-serif; }

@mixin helvetica {
	font-family: helvetica, Arial, sans-serif; }
@mixin helvetica-oblique {
	font-family: helvetica-oblique, Arial, sans-serif; }
@mixin helvetica-bold {
	font-family: helvetica-bold, Arial, sans-serif; }

@mixin poppins-semibold {
	font-family: poppins-semibold, Arial, sans-serif; }

@mixin lora-italic {
	font-family: lora-italic, Arial, sans-serif; }

@mixin greatVibes-regular {
	font-family: greatVibes-regular, Arial, sans-serif; }

body {
	background: #FFF;
	@include css-lock(16, 50, 600, 1000);
	line-height: 1.4; }

