.deals {
    margin: 0px auto;
    position: relative;
    @include clr;
    &__wrapper {
        max-width: 1170px;
        margin: 0 auto;
        .wrapper {
            max-width: 1170px;
            .pages {
                .page {
                    height: 3px; } } }
        @include r(1200) {
            margin: 0 20px; } }
    &__header {
        margin: 0 auto;
        margin-top: 50px;
        @include clr; }
    &__bottom {
        margin: 0 auto;
        margin-top: 50px; }
    &__style {
        margin: 0 auto;
        padding: 60px;
        position: relative;
        text-align: center;
        img {
            width: 100%;
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            max-width: 100%; } }
    &__decor, &__taste {
        margin: 0 auto;
        padding: 0;
        text-align: center;
        background-color: #ffffff; }
    &__taste {
        font-size: 16px;
        @include roboto-medium;
        color: #525252;
        .fa-star {
            color: #8dd3a5; } }
    &__left {
        display: inline-block;
        float: left;
        width: 40.42735042735042735042735042735%;
        h3 {
            margin-top: 30px;
            font-size: 12px;
            @include roboto-medium;
            color: #525252;
            line-height: 1.2;
            text-align: right;
            letter-spacing: 2px; } }
    &__name {
        margin-top: 30px;
        padding-left: 20%;
        text-align: right;
        font-size: 26px;
        @include roboto-medium;
        color: #525252;
        line-height: 1.2; }
    &__img {
        display: inline-block;
        float: left;
        width: 19.145299145299145299145299145299%;
        img {
            max-width: 100%;
            width: 100%; } }
    &__right {
        display: inline-block;
        float: right;
        width: 40.42735042735042735042735042735%;
        p {
            font-size: 14px;
            margin-top: 30px;
            padding-right: 20%;
            @include helvetica;
            color: #525252;
            line-height: 1.8;
            text-align: left; } }
    &__row {
        height: 370px; }
    &__menu {
        margin: 0 auto;
        margin-top: 50px;
        width: 130px;
        @include clr;
        ul {
            li {
                width: 33.333333333333333333333333333333%;
                float: left;
                a {
                    margin: 0 3px;
                    width: 26px;
                    height: 26px;
                    padding: 3px;
                    text-align: center;
                    float: left;
                    display: inline-block;
                    color: #dedede;
                    font-size: 1rem;
                    background-color: #ffffff;
                    border-radius: 50%; }
                a:hover {
                    background-color: #90d3a6;
                    color: #ffffff; } } } }
    &__decor-left {
        position: absolute;
        left: 0;
        top: 85%;
        transform: translateY(-85%);
        width: 100%; }
    &__decor-right {
        position: absolute;
        right: 0;
        top: -10%;
        transform: translateY(10%);
        max-width: 100%; } }

.times {
    margin: 30px auto;
    text-align: center;
    width: 100%;
    .time {
        display: inline-block;
        height: 80px;
        width: 80px;
        border-top: 6px solid #a6d3a0;
        border-bottom: 6px solid #5dc6cf;
        border-radius: 12px;
        background-position: 0 0, 100% 0;
        background-repeat: no-repeat;
        background-size: 6px 100%;
        box-sizing: border-box;
        background-image: linear-gradient(to bottom, #a6d3a0 0%, #5dc6cf 100%), linear-gradient(to bottom, #a6d3a0 0%, #5dc6cf 100%);
        &__days, &__hours, &__mins, &__secs {
            font-size: 40px;
            @include helvetica;
            color: #525252;
            font-weight: bold;
            line-height: 1.2;
            text-align: center;
            margin: 0;
            padding: 0; }
        &__text {
            margin: 0;
            padding: 0;
            font-size: 14px;
            @include helvetica;
            color: #75d1d1;
            font-weight: bold;
            line-height: 0.8;
            text-align: center; } } }

.deal {
    background-color: #ffffff;
    width: 48.717948717948717948717948717949%;
    height: 252px;
    border-radius: 15px;
    @include clr;
    &__part1 {
        float: left;
        margin-right: 2.5641025641025641025641025641026%;
        @include clr; }
    &__part2 {
        float: right;
        @include clr; }
    &__likes {
        width: 60px;
        float: left;
        display: inline-block;
        a {
            float: left;
            width: 100%;
            height: 78px;
            text-align: center;
            padding: 20px;
            font-size: 1rem;
            color: #dedede;
            border-right: 2px solid #dedede;
            @include clr; } }
    &__body {
        float: left;
        display: inline-block;
        width: 305px;
        padding-top: 30px;
        padding-left: 30px; }
    &__about {
        margin-top: 20px;
        font-size: 10px;
        @include roboto-medium;
        color: #a0a0a0;
        line-height: 1.2;
        text-align: left;
        letter-spacing: 3px; }
    &__name {
        margin-top: 20px;
        font-size: 30px;
        @include roboto-medium;
        color: #525252;
        line-height: 1.2;
        text-align: left; }
    &__text {
        margin-top: 15px;
        font-size: 14px;
        @include helvetica;
        color: #525252;
        line-height: 1.8;
        text-align: left; }
    &__price {
        margin-top: 15px;
        font-size: 14px;
        @include helvetica;
        color: #7fba00;
        font-weight: bold;
        line-height: 1.2;
        text-align: left; }
    &__img {
        float: left;
        display: inline-block;
        width: 205px;
        height: 270px;
        @include clr;
        img {
            width: 100%; } } }

.old_price {
    display: inline-block;
    color: #dedede;
    text-decoration: line-through; }
