.footer {
    margin: 0 auto;
    margin-top: 50px;
    padding-top: 50px;
    @include clr;
    &__wrapper {
        margin: 0 auto;
        max-width: 1170px;
        @include clr;
        @include r(1200) {
            margin: 0 20px; } }
    &__item {
        width: 31.623931623931623931623931623932%;
        float: left;
        margin-right: 2.5641025641025641025641025641026%;
        h1 {
            font-size: 12px;
            @include roboto-medium;
            color: #525252;
            line-height: 1.4;
            text-align: left; }
        ul {
            li {
                line-height: 20px;
                a {
                    font-size: 10px;
                    @include roboto-medium;
                    color: #525252;
                    line-height: 1.4;
                    text-align: left; } } }
        .information, .account {
            padding: 15px;
            float: left;
            width: 45.945945945945945945945945945946%;
            @include clr; }
        .information {
            margin-right: 8.1081081081081081081081081081081%;
            @include r(1200) {
                margin-right: 1%;
                width: 53%; } }
        .instagram {
            padding-top: 15px;
            &__gallery {
                margin-top: 10px; } }
        @include r(700) {
            max-width: 400px;
            width: 100%;
            float: none;
            margin: 25px auto;
            @include clr; } }
    &__item:nth-child(3n+3) {
        margin-right: 0;
        float: right;
        @include r(700) {
            float: none;
            max-width: 400px;
            width: 100%;
            margin: 0 auto;
            margin-top: 30px; } }
    &__logo {
        float: none;
        @include clr;
        .logo {
            margin: 0;
            padding: 0; } }
    &__hr {
        margin-top: 25px;
        @include clr;
        hr {
            float: left;
            width: 30px;
            color: #84c400;
            background-color: #84c400;
            height: 3px;
            @include clr; } }
    &__text {
        margin: 15px 0;
        font-size: 11px;
        @include helvetica;
        color: #525252;
        line-height: 1.5;
        text-align: left;
        @include clr; }
    &__style {
        margin: 0 auto;
        margin-top: 50px;
        position: relative;
        text-align: center;
        img {
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            width: 100%; } } }

.contact {
    font-size: 15px;
    @include roboto-medium;
    color: #525252;
    font-weight: bold;
    line-height: 1.85;
    text-align: left;
    div {
        display: inline-block; } }

.email {
    a {
        color: #525252; } }

.location {
    a {
        color: #84c400; } }

.gallery__img {
    width: calc((100% - 25px * 3) / 3);
    margin-right: 10px;
    margin-left: 10px;
    float: left;
    @include r(920) {
        width: calc((100% - 25px * 2) / 2); } }


.footer__bottom {
    margin: 0px auto;
    text-align: center;
    max-width: 1170px;
    font-size: 15px;
    font-family: "Myriad Pro", sans-serif;
    color: #999999;
    text-align: center;
    position: relative;
    @include r(1200) {
        margin: 0 20px; }
    .bottom {
        position: absolute;
        top: 0;
        transform: translateY(-50%);
        width: 100%;
        @include r(700) {
            position: relative;
            transform: translateY(0%); } }
    .arrow {
        position: absolute;
        margin-top: -3rem;
        cursor: pointer;
        transition: all .5s;
        left: 0;
        right: 0;
        width: 100%;
        &:hover {
            color: rgba(0, 0, 0, 0.5); }
        @include r(1400) {
            margin-top: -30px; }
        @include r(700) {
            margin-top: -15px; } }
    .allrights {
        float: left;
        width: 30%;
        text-align: left;
        @include clr;
        @include r(700) {
            float: none;
            width: 100%;
            margin-top: 10px;
            text-align: center; } }
    .bottom__menu {
        float: right;
        width: 70%;
        text-align: right;
        display: inline-block;
        @include clr;
        ul {
            text-align: right;
            li {
                display: inline-block;
                font-size: 15px;
                @include roboto-medium;
                line-height: 1.4;
                text-align: right;
                @include r(700) {
                    display: block;
                    text-align: center; }
                a {
                    float: left;
                    color: #545454;
                    text-align: right;
                    margin-right: 10px;
                    transition: all .5s;
                    @include clr;
                    @include r(700) {
                        float: none;
                        width: 100%;
                        text-align: center; } }
                a:hover {
                    color: #525252; } } }
        @include r(700) {
            float: none;
            width: 100%;
            margin-top: 10%; } } }
