.farmfood {
    position: relative;
    margin: 0 auto;
    margin-bottom: 200px;
    @include clr;
    &__wrapper {
        margin: 0 auto;
        text-align: center;
        max-width: 1170px;
        position: relative;
        @include r(1200) {
            margin: 0 20px; } }
    &__title {
        position: relative; }
    &__leaf {
        position: absolute;
        top: 27px;
        left: 50%;
        transform: translateX(-50%); }
    &__about {
        font-size: 12px;
        @include roboto-medium;
        letter-spacing: 2px;
        color: #525252;
        line-height: 1.2;
        text-align: center; }
    &__text {
        font-size: 16px;
        margin: 25px 0;
        @include helvetica;
        color: #525252;
        line-height: 1.8;
        text-align: center; }
    &__light {
        font-size: 40px;
        @include roboto-light;
        font-weight: 200;
        color: #525252;
        line-height: 1.2;
        text-align: center; }
    &__black {
        font-size: 40px;
        color: #525252;
        line-height: 1.2;
        text-align: center;
        @include roboto-light;
        font-weight: 900; }
    &__decor-left {
        position: absolute;
        left: 0;
        top: 25%;
        transform: translateY(-25%);
        width: 100%; }
    &__decor-right {
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%); }
    &__decor-bottom {
        position: absolute;
        bottom: -200px;
        left: 50%;
        transform: translateX(-50%);
        img {
            width: 100%;
            text-align: center; } } }

.features {
    width: 100%;
    text-align: center;
    margin: 0 auto; }

.feature {
    float: left;
    width: 25%;
    // margin: 0 0.9615384615384615384615384615385%
    @include clr;
    &__title {
        font-size: 18px;
        @include roboto-medium;
        color: #525252;
        line-height: 1.2;
        text-align: center; }
    &__text {
        font-size: 14px;
        @include helvetica;
        color: #525252;
        line-height: 1.5;
        text-align: center; }
    &-edge {
        margin: 200px 0; }
    &-center {
        margin: 70px 0; } }

.readmore {
    width: 125px;
    height: 38px;
    background-color: #ffffff;
    font-size: 12px;
    @include roboto-medium;
    color: #bac3c3;
    line-height: 1.2;
    text-align: center;
    border-radius: 25px; }
