.subscribe {
    position: relative;
    margin: 0px auto;
    overflow: hidden;
    @include clr;
    &__wrapper {
        max-width: 1170px;
        margin: 0px auto;
        height: 260px;
        @include r(1200) {
            margin: 0 20px; } }
    &__decor-left {
        position: absolute;
        margin: 0;
        left: 0;
        bottom: 0;
        img {
            position: absolute;
            margin: 0;
            left: 0;
            bottom: 0;
            width: 100%; } }
    &__decor-right {
        position: absolute;
        top: 0px;
        right: 0;
        z-index: 99;
        @include r(500) {
            display: none; } }
    &__label, &__form {
        display: inline-block;
        @include r(470) {
            width: 100%; } }
    &__label {
        margin-top: 100px;
        float: left;
        width: 31.623931623931623931623931623932%;
        margin-right: 2.5641025641025641025641025641026%;
        @include r(1000) {
            margin-top: 60px; }
        @include r(470) {
            margin-top: 10px; } }
    &__form {
        margin-top: 80px;
        float: right;
        width: 65.811965811965811965811965811966%;
        z-index: 9999;
        @include r(1000) {
            margin-top: 20px; }
        @include r(470) {
            margin-top: 10px; } }
    &__about {
        font-size: 12px;
        letter-spacing: 2px;
        @include roboto-medium;
        color: #525252;
        line-height: 1.2;
        text-align: left; }
    &__title {
        font-size: 20px;
        letter-spacing: 2px;
        margin-top: 10px;
        @include roboto-medium;
        color: #525252;
        line-height: 1.2;
        text-align: left; }
    &__email {
        float: left;
        max-width: 551px;
        height: 58px;
        width: 67.5%;
        margin-right: 2.5%;
        input[type='email'] {
            height: 100%;
            width: 100%;
            border: none;
            border-radius: 40px;
            padding: 12px 20px;
            font-size: 15px;
            @include roboto-medium;
            color: #525252;
            line-height: 1.2;
            text-align: left;
            background-color: #ffffff; }
        input[type='email']:placeholder {
            color: #cccccc; }
        @include r(1000) {
            width: 100%; }
        @include r(470) {
            margin-top: 10px; } }
    &__btn {
        max-width: 200px;
        width: 30%;
        float: left;
        position: relative;
        z-index: 9999;
        button[type=submit] {
            height: 58px;
            width: 200px;
            background-color: #7cc000;
            color: #ffffff;
            margin-top: 17px;
            padding: 12px 20px;
            border: none;
            border-radius: 40px;
            font-size: 17px;
            @include roboto-medium;
            cursor: pointer;
            float: right;
            .fa-long-arrow-alt-right {
                margin-left: 10px; }
            @include r(470) {
                margin-top: 0px; } }
        button:hover {
            background-color: #45a049; }
        @include r(1000) {
            width: 100%;
            margin-top: 15px; }
        @include r(470) {
            margin-top: 5px; } } }
