.ourproducts {
    margin: 50px auto;
    max-width: 1170px;
    box-sizing: border-box;
    @include clr;
    @include r(1200) {
        margin: 0 20px; }
    &__one, &__two {
        margin: 0 auto;
        margin-top: 50px;
        @include clr; }
    &__one {
        .ourproduct {
            // 30px / 1170px
            margin-right: 2.5641025641025641025641025641026%;
            &__descript {
                padding-left: 30px; } } }

    &__two {
        .ourproduct {
            // 30px / 1170px
            margin-left: 2.5641025641025641025641025641026%;
            &__descript {
                padding-right: 30px; } } } }

.ourproduct {
    float: left;
    // 170px / 1170px =
    width: 14.529914529914529914529914529915%;
    height: 300px;
    @include clr;
    &__descript {
        float: left;
        // 570px / 1170px
        width: 48.717948717948717948717948717949%;
        @include clr; }
    &__logo {
        text-align: center;
        width: 100%;
        height: 175px;
        img {
            max-width: 100%;
            margin: 0 auto; } }
    &__line {
        width: 40px;
        height: 5px;
        margin: 15px auto;
        border-top: 3px solid #eeeeee; }
    &__title {
        font-size: 18px;
        @include roboto-medium;
        color: #525252;
        line-height: 1.2;
        text-align: center;
        margin: 10px auto; }
    &__items {
        font-size: 13px;
        @include helvetica-oblique;
        color: #7fb900;
        line-height: 1.2;
        text-align: center; } }

.ourdescript {
    @include clr;
    &__about {
        font-size: 12px;
        @include roboto-medium;
        color: #525252;
        line-height: 1.2;
        text-align: left; }
    &__title {
        margin-top: 30px;
        margin-bottom: 30px;
        font-size: 24px;
        @include roboto-medium;
        color: #525252;
        line-height: 1.3;
        text-align: left; }
    &__text {
        font-size: 16px;
        @include helvetica;
        color: #525252;
        line-height: 1.8;
        text-align: left; } }
