.random {
    @include clr;
    &__wrapper {
        max-width: 1170px;
        margin: 0 auto;
        text-align: center;
        @include clr;
        @include r(1200) {
            margin: 0 20px; } }
    &__part1 {
        float: left;
        width: 31.623931623931623931623931623932%;
        margin-right: 2.5641025641025641025641025641026%;
        @include clr; }
    &__part1:nth-child(3n+3) {
        margin-right: 0;
        float: right;
        @include clr; }
    &__about {
        font-size: 10px;
        @include roboto-medium;
        color: #9f9f9f;
        line-height: 1.2;
        text-align: left;
        letter-spacing: 2px;
        @include clr; }
    &__top {
        margin: 10px 0 30px 0;
        @include clr; }
    &__title {
        float: left;
        display: inline-block;
        font-size: 18px;
        @include roboto-medium;
        color: #525252;
        line-height: 1.2;
        text-align: left;
        @include clr; }
    &__buttons {
        height: 27px;
        width: 74px;
        border: 2px solid #dedede;
        border-radius: 40px;
        font-size: 1rem;
        float: right;
        @include clr;
        .left, .right {
            margin: 0;
            padding: 0;
            color: #525252;
            display: inline-block;
            line-height: 1;
            @include clr;
            .fa-long-arrow-alt-left, .fa-long-arrow-alt-right {
                font-size: 1rem; } } } }

.cart {
    border: 2px solid #dedede;
    border-radius: 10px;
    height: 120px;
    overflow: hidden;
    margin-top: 30px;
    line-height: 1;
    padding: 0;
    box-sizing: border-box;
    @include clr;
    &__img {
        float: left;
        // width: 110px
        text-align: left;
        display: inline-block;
        padding: 3px;
        @include clr; }
    &__descript {
        float: left;
        display: inline-block;
        height: 160px;
        padding-left: 10px;
        @include clr; }
    &__title {
        margin-top: 20px;
        font-size: 18px;
        @include roboto-medium;
        color: #525252;
        line-height: 1.2;
        text-align: left;
        @include clr; }
    &__dash {
        width: 30px;
        margin-top: 20px;
        border: 1px solid #dedede;
        @include clr; }
    &__price {
        margin-top: 15px;
        font-size: 14px;
        @include helvetica;
        color: #7fbb00;
        font-weight: bold;
        line-height: 1.2;
        text-align: left;
        @include clr; } }
